import Vue from 'vue'
import Router from 'vue-router'

import Home from '../views/Home';
import ChangeLog from '../views/ChangeLog';
import dialogHome from '../views/v-dialogs/Home';
import dialogDemo from '../views/v-dialogs/Demo';

import pageHome from '../views/v-page/Home';
import pageDemo from '../views/v-page/Demo';

Vue.use(Router);


export default new Router({
    routes: [
        {path:'/',component: Home},
        {path:'/changelog',component: ChangeLog},
        {path:'/dialog/',component: dialogHome},
        {path:'/dialog/demo',component: dialogDemo},
        {path:'/page',component: pageHome},
        {path:'/page/demo',component: pageDemo}
    ]
});


