import Vue from 'vue';
import Vuex from 'vuex';
import changeLog from './changelog';

Vue.use(Vuex);

let baseImgUrl = 'terryz.oschina.io';
//host 和 hostname 的区别在于，hostname不带端口号
if(window.location.hostname === 'terryz.github.io')
    baseImgUrl = 'terryz.github.io';

const store = new Vuex.Store({
    strict: true,  // process.env.NODE_ENV !== 'production',
    state:{
        currentProject: '',
        language: 'en',
        changeLog: changeLog,
        baseImgUrl: `https://${baseImgUrl}`
    },
    mutations: {
        changeProject: (state,val) => state.currentProject = val
    }
})

export default store;


