<template>
    <div id="app">
        <header-bar></header-bar>
        <div class="bodyBox">
            <router-view></router-view>
        </div>
        <footer-bar></footer-bar>
    </div>
</template>

<script>
    import headerBar from './views/Header';
    import footer from './views/Footer';

    export default {
        name: 'app',
        components: {
            'header-bar': headerBar,
            'footer-bar': footer
        }
    }
</script>

<style lang="scss">
    body{
        background-color: #727f80;
    }
    button {
        outline: 0 !important;
    }
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
        /*margin-top: 60px;*/
    }
    h1, h2 { font-weight: normal; }
    a { color: #42b983; }

    .bodyBox {
        background-color: white;
    }

    .container {
        width: 980px;
    }

    div.pluginHome {
        padding: 50px 0 100px;
        h1.heading{
            padding-bottom: 50px;
        }
    }

    div.home {
        .headerBox{
            /*background-color: #0866C6;*/
            div.headerArea{
                /*background-color: #0866C6;*/
            }
        }
        .headerArea {
            .jumbotron {
                margin-left: 280px;
                background-color: white;
                margin-bottom: 0;
                padding: 0 0 20px 0;
                h1,h3 {
                    color: black;
                }
                h1 {
                    font-size: 40px;
                    font-weight: lighter;
                }
                h3 small{
                    color: #DDDDDD;
                }
                p {
                    color: black;
                    font-size: 18px;
                }
            }
        }


        .content{
            background-color: white;
            .container {
                width: 900px;
            }
            .row {
                .col{
                    padding: 0 100px;
                }
                h3 {
                    color: #222222;
                    font-size: 20px;
                }
                .desc{
                    font-size: 14px;
                    color: #666666;
                }
            }
        }

        .pluginImg{
            text-align: right;
            img{
                margin-top: 50px;
            }
        }
    }
</style>



