const keys = {
    'Home': 'Home',
    'vPage': 'vPage',
    'vDialogs': 'vDialogs'
    /*'vTableGrid': 'v-tablegrid',*/
    /*'vSelectPage': 'v-selectpage',*/
    /*'vSelectMenu': 'v-selectmenu'*/
};
const urls = [
    {
        home: '/',
        key: keys.Home,
        name: 'home'
    }, {
        home: '/page',
        demo: '/page/demo',
        key: keys.vPage,
        name: 'v-page'
    }, {
        home: '/dialog',
        demo: '/dialog/demo',
        key: keys.vDialogs,
        name: 'v-dialogs'
    }
];
const getProject = (path) => {
    if(!path) return;
    return urls.find(val => {
        return (path === val.home && val.home === '/') || (val.home!=='/' && path.startsWith(val.home));
    });
};

export { keys };
export { urls };
export { getProject };


